class bluetooth {
	constructor(arg) {}
	/** 
	 * 
	 * @param {Object} str
	 * @return {Boolean}  
	 * @author xxx 
	 */
	openBluetoothAdapter(_this, callback) {
		var that = _this;
		var _this = this;
		wx.showLoading({
			title: ''
		});
		wx.openBluetoothAdapter({
			success: function(res) {
				console.log("" + JSON.stringify(res));
				_this.getBluetoothAdapterState(that);
				callback(true, res);
			},
			fail: function(res) {
				console.log(res);
				callback(false, res);
			},
			complete: function() {
				setTimeout(function() {
					wx.hideToast()
				}, 2000);
			}
		});
		//
		wx.onBluetoothAdapterStateChange(function(res) {
			var available = res.available;
			console.log(res.discovering ? "" : "");
			console.log(res.available ? "" : "");
			if (available) {
				_this.getBluetoothAdapterState(that);
			}
		});
	}

	// 
	getBluetoothAdapterState(_this) {
		var that = _this;
		wx.getBluetoothAdapterState({
			success: function(res) {
				that.msg = "" + "/" + JSON.stringify(res.errMsg);
				console.log(res.discovering ? "" : "");
				console.log(res.available ? "" : "");
				console.log(that.msg);
				if (!res.available) {
					wx.showToast({
						title: '',
						icon: 'success',
						duration: 2000
					});
					setTimeout(function() {
						wx.hideToast()
					}, 2000);
					that.bluetoothOpenFlag = false;
				} else {
					if (that.bluetoothOpenFlag) {
						that.initOpen();
						that.bluetoothOpenFlag = true;
					}
				}
			},
			fail: function(res) {
				console.log(res)
			}
		})
	}
	/** 
	 * 
	 * @param {Object} str
	 * @return {Boolean}  
	 * @author xxx 
	 */
	startBluetoothDevicesDiscovery(_this, callback) {
		var that = _this;
		that.devices = [];
		wx.startBluetoothDevicesDiscovery({
			success: function(res) {
				//
				wx.onBluetoothAdapterStateChange(function(res) {
					console.log(res.discovering ? "" : "");
				});
				console.log(":", res);
				callback(true, res);
			},
			fail: function(res) {
				callback(false, res);
				console.log("", res)
			}
		});

	}
	/** 
	 * 
	 * @param {Object} str
	 * @return {Boolean}  
	 * @author xxx 
	 */
	getBluetoothDevices(_this) {
		var that = _this;
		that.devices = [];
		let deviceId = that.connectedDeviceId;
		wx.getBluetoothDevices({
			success: function(res) {
				// uuid 
				wx.getConnectedBluetoothDevices({
					success: function(res) {
						console.log("", JSON.stringify(res));
						if (res.devices[0]) {
							console.log("");
						}
						that.connectedDeviceId = res.deviceId;
						// 5 s  
						setTimeout(function() {
							if (!that.deviceId) {
								//
								console.log('');
								that.stopFindBLEDevices();
								setTimeout(function() {
									that.initOpen();
								}, 2000);
							}
						}, 5000);
					},
					fail: function(res) {
						console.log("", res)
					}
				})
				let devices = []
				for (let i = 0; i < res.devices.length; i++) {
					if (res.devices[i].name != "") {
						devices.push(res.devices[i])
					}
				}
				that.devices = devices;
				console.log("", res.devices);
				// that.msg = "" + JSON.stringify(res.devices);
				//
				wx.onBluetoothAdapterStateChange(function(res) {
					console.log(res.discovering ? "" : "");
					console.log(res.available ? "" : "");
				})
			},
			fail: function(res) {
				console.log(res)
			}
		})
	}
	/** 
	 * 
	 * @param {Object} str
	 * @return {Boolean}  
	 * @author xxx 
	 */
	onBluetoothDeviceFound(_this, callback) {
		let that = _this;
		wx.onBluetoothDeviceFound(function(res) {
			// console.log(':', res.devices)
			if (that.find_status) {
				return;
			}
			for (let i = 0; i < res.devices.length; i++) {
				//
				if (res.devices[i].name.indexOf('GSD-') != -1) {
					let connectedDeviceInfo = res.devices[i];
					// console.log("mac:", ab2hex(res.devices[i].advertisData));
					let driver_mac = ab2hex(res.devices[i].advertisData);
					//
					that.find_status = true;
					console.log(':', res.devices[i].deviceId, '', res.devices[i].name);
					that.driver_name = res.devices[i].name;
					callback(true, connectedDeviceInfo, driver_mac);
					return;
				} else {
					callback(false, '');
				}
			}
		});
		wx.onBeaconUpdate(function(res) {
			console.log('', res);
		});
	}
	/** 
	 * 
	 * @param {Object} str
	 * @return {Boolean}  
	 * @author xxx 
	 */
	stopBluetoothDevicesDiscovery(_this, callback) {
		var that = _this;
		wx.stopBluetoothDevicesDiscovery({
			success: function(res) {
				console.log("" + "/" + JSON.stringify(res.errMsg))
				callback(true, res)
			},
			fail: function(res) {
				console.log("", res);
				callback(false, res)
			}
		})
	}
	/** 
	 * 
	 * @param {Object} str
	 * @return {Boolean}  
	 * @author xxx 
	 */
	connectTO(deviceId, _this, callback) {
		var that = _this;
		if (typeof deviceId == "object") {
			deviceId = deviceId.currentTarget.id;
			that.connectedDeviceId = deviceId;
		}
		if (!deviceId) {
			console.log("id")
			return false;
		}
		console.log("id:", deviceId);
		wx.createBLEConnection({
			deviceId: deviceId.toString(),
			success: function(res) {
				wx.onBLEConnectionStateChange(function(res) {
					// 
					console.log(`device ${res.deviceId} state has changed, connected: ${res.connected}`)
				});
				callback(true, res);
				console.log("-id%s,", that.connectedDeviceId, res);
			},
			fail: function(res) {
				callback(false, res);
				console.log("", res);
			},
			complete: function() {
				console.log("");
			}
		});
	}
	/** 
	 * service
	 * @param {Object} str
	 * @return {Boolean}  
	 * @author xxx 
	 */
	getBLEDeviceServices(_this, callback) {
		var that = _this;
		let deviceId = that.connectedDeviceId.toString();
		console.log(deviceId);
		wx.getBLEDeviceServices({
			//  deviceId  getBluetoothDevices  onBluetoothDeviceFound 
			deviceId: deviceId,
			success: function(res) {
				console.log('service:', res);
				that.services = res.services;
				callback(true)
			},
			fail: function(res) {
				console.log("service", res);
				callback(false)
			},
		})
	}
	//
	getBLEDeviceCharacteristics(_this, callback) {
		var that = _this;
		let deviceId = that.connectedDeviceId;
		let serverid = "00001000-0000-1000-8000-00805F9B34FB";
		// let serverid = "0000180A-0000-1000-8000-00805F9B34FB";
		wx.getBLEDeviceCharacteristics({
			//  deviceId  getBluetoothDevices  onBluetoothDeviceFound 
			deviceId: deviceId,
			//  serviceId  getBLEDeviceServices 
			// serviceId: that.services[0].uuid,
			serviceId: serverid,
			success: function(res) {
				console.log(':', res);
				// that.getBLEValueChange();
				callback(true);
			},
			fail: function(res) {
				console.log("", res);
				callback(false);
			},
			complete: function() {
				// console.log("complete");
			}
		});
	}
	// notify 
	notifyBLEValueChange(_this, callback) {
		var that = _this;
		let deviceId = that.connectedDeviceId.toString();
		let notifyServicweId = that.notifyServicweId.toUpperCase(); //UUID
		let notifyCharacteristicsId = that.notifyCharacteristicsId.toUpperCase(); //UUID
		console.log("deviceId", deviceId);
		console.log("notifyServicweId", notifyServicweId);
		console.log("notifyCharacteristicsId", notifyCharacteristicsId);
		wx.notifyBLECharacteristicValueChange({
			state: true, //  notify 
			//  deviceId  getBluetoothDevices  onBluetoothDeviceFound 
			deviceId: deviceId,
			//  serviceId  getBLEDeviceServices 
			serviceId: notifyServicweId,
			//  characteristicId  getBLEDeviceCharacteristics 
			characteristicId: notifyCharacteristicsId,
			success: function(res) {
				console.log('notify ', res);
				callback(true, res);
			},
			fail: function(res) {
				callback(false, res);
				console.log('notify ', res);
			}
		})
	}
	//
	sendBLEValueChange(_this, inputValue, callback) {
		var that = _this;
		let deviceId = that.$store.state.connectedDeviceId;
		//  write 
		wx.onBLECharacteristicValueChange(function(res) {
			let hex = ab2hex(res.value);
			console.log(",", hex, res);
			callback(true, 'result', hex);
		});
		let senddata = inputValue;
		console.log(senddata, deviceId)
		let buffer = new ArrayBuffer(3)
		let dataView = new DataView(buffer)
		dataView.setUint8(0, senddata);
		console.log('', dataView);
		console.log('writeServicweId', that.writeServicweId);
		console.log('writeCharacteristicsId', that.writeCharacteristicsId);
		wx.writeBLECharacteristicValue({
			//  deviceId  getBluetoothDevices  onBluetoothDeviceFound 
			deviceId: deviceId,
			//  serviceId  getBLEDeviceServices 
			serviceId: that.writeServicweId,
			//  characteristicId  getBLEDeviceCharacteristics 
			characteristicId: that.writeCharacteristicsId,
			// valueArrayBuffer
			value: buffer,
			success: function(res) {
				callback(true, 'success', '');
				console.log('', res)
			},
			fail: function(res) {
				callback(false, '', res);
				console.log('', res);
			}
		});
	}
	//
	getBLEValueChange(_this, callback) {
		var that = _this;
		let deviceId = that.connectedDeviceId.toString();
		// 
		wx.onBLECharacteristicValueChange(function(characteristic) {
			let hex = ab2hex(characteristic.value);
			console.log(",", hex, characteristic);
		})
		console.log("readServicweId", that.serviceuuid);
		console.log("readCharacteristicsId", that.readuuid);
		wx.readBLECharacteristicValue({
			//  deviceId  getBluetoothDevices  onBluetoothDeviceFound 
			deviceId: deviceId,
			//  serviceId  getBLEDeviceServices 
			serviceId: that.serviceuuid,
			//  characteristicId  getBLEDeviceCharacteristics 
			characteristicId: that.readuuid,
			success: function(res) {
				callback(true, res);
				console.log(':', res);
			},
			fail: function(res) {
				callback(false, res);
				console.log('', res);
			}
		})
	}
	//
	closeBLEConnection(_this) {
		var that = _this;
		let deviceId = that.$store.state.connectedDeviceId;
		if (deviceId.length > 0) {
			deviceId = deviceId.toString();
		} else {
			console.log("id")
			return;
		}
		wx.closeBLEConnection({
			deviceId: deviceId,
			success: function(res) {
				that.connectedDeviceId = "";
				console.log("", res)
			},
			fail: function(res) {
				console.log("", res)
			}
		})
		wx.closeBluetoothAdapter({
			success(res) {
				console.log('', res);
			},
			fail: function(err) {
				console.log('', err)
			}
		});
	}
}
// ArrayBuffer16
function ab2hex(buffer) {
	var hexArr = Array.prototype.map.call(
		new Uint8Array(buffer),
		function(bit) {
			return ('00' + bit.toString(16)).slice(-2)
		}
	)
	return hexArr;
}
// 16ASCLL
function hexCharCodeToStr(hexCharCodeStr) {
	var trimedStr = hexCharCodeStr.trim();
	var rawStr = trimedStr.substr(0, 2).toLowerCase() === "0x" ? trimedStr.substr(2) : trimedStr;
	var len = rawStr.length;
	var curCharCode;
	var resultStr = [];
	for (var i = 0; i < len; i = i + 2) {
		curCharCode = parseInt(rawStr.substr(i, 2), 16);
		resultStr.push(String.fromCharCode(curCharCode));
	}
	return resultStr.join("");
}
export default new bluetooth()



